import { connect } from "react-redux";
import {
  follow,
  unfollow,
  setUsers,
  setTotalCount,
  changeCurrentPage,
  isFetchingToggle,
  disableProcess,
  getUsersThunk,
  getChangedUsersThunk,
  followThunk, 
  unFollowThunk
} from "../../redux/users-Reducer";
import Users from "./users";
import React from "react";
import { compose } from "redux";
import { withAuthRedirect } from "../Hoc/withAuthRedirect";
import { pageSize, totalCount, pageCount, currentPage, isFetching, disableProcessState, disableID, getUsersSelector, portionCount } from "../../redux/users-selector";

class UsersAPI extends React.Component {
  componentDidMount() {
    if (this.props.users.length === 0) {
      this.props.getUsersThunk(this.props.currentPage,this.props.pageSize)
    }
  }
  changeCurrentPage = e => {
    this.props.getChangedUsersThunk(e,this.props.pageSize)
    
  };
  render() {
    return (
      <Users
        totalCount={this.props.totalCount}
        pageSize={this.props.pageSize}
        portionCount={this.props.portionCount}
        currentPage={this.props.currentPage}
        changeCurrentPage={this.changeCurrentPage}
        users={this.props.users}
        isFetching={this.props.isFetching}
        disableProcessState={this.props.disableProcessState}
        disableID= {this.props.disableID}
        followThunk={this.props.followThunk}
        unFollowThunk={this.props.unFollowThunk}
      />
    );
    
  }
}

let mapToStateProps = state => {
  return {
    users: getUsersSelector(state),
    pageSize: pageSize(state),
    portionCount: portionCount(state),
    totalCount: totalCount(state),
    pageCount: pageCount(state),
    currentPage: currentPage(state),
    isFetching: isFetching(state),
    disableProcessState: disableProcessState(state),
    disableID: disableID(state)
  };
};


export default compose(
  connect(mapToStateProps, {
    follow,
    unfollow,
    setUsers,
    setTotalCount,
    changeCurrentPage,
    isFetchingToggle,
    disableProcess,
    getUsersThunk,
    getChangedUsersThunk,
    followThunk,
    unFollowThunk
  }),
  // withAuthRedirect
)(UsersAPI)