import React, { useState } from "react";
import s from "./paginator.module.css";

const Paginator = (props) => {
  let pageCount = Math.ceil(props.totalCount / props.pageSize);
  let pages = [];
  for (let i = 1; i <= pageCount; i++) {
    pages.push(i);
  }
  let portionSize = Math.ceil(pageCount / props.portionCount);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionNumber = (portionNumber - 1) * props.portionCount + 1;
  let rightPortionNumber = portionNumber * props.portionCount;

  return (
    <div>
      {portionNumber > 1 && (
        <button onClick={() => setPortionNumber(portionNumber - 1)}>
          Prev
        </button>
      )}
      {pages
        .filter((p) => p >= leftPortionNumber && p <= rightPortionNumber)
        .map((e) => {
          return (
            <button
              key={e}
              disabled={
                e === props.disableID ? props.disableProcessState : false
              }
              className={props.currentPage === e ? s.active : undefined}
              onClick={() => props.changeCurrentPage(e)}
            >
              {e}
            </button>
          );
        })}
      {portionNumber < portionSize && (
        <button
          onClick={() => {
            setPortionNumber(portionNumber + 1);
            props.changeCurrentPage(rightPortionNumber+1)
          }}
        >
          Next
        </button>
      )}
    </div>
  );
};

export default Paginator;
