import { createSelector } from "reselect"

export const getUsers = (state) => {
    return state.usersPage.users
}
export const getUsersSelector = createSelector(getUsers, (users) =>{
        return users.filter(u => true)
    }
)
export const pageSize = (state) => {
    return state.usersPage.pageSize
}
export const portionCount = (state) => {
    return state.usersPage.portionCount
}
export const totalCount = (state) => {
    return state.usersPage.totalCount
}
export const pageCount = (state) => {
    return state.usersPage.pageCount
}
export const currentPage = (state) => {
    return state.usersPage.currentPage
}
export const isFetching = (state) => {
    return state.usersPage.isFetching
}
export const disableProcessState = (state) => {
    return state.usersPage.disableProcessState
}
export const disableID = (state) => {
    return state.usersPage.disableID
}
